body {
  margin: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

input {
  width: 280px;
  height: 28px;
  border-radius: 3px;
  padding: 0 10px;
}

.search-form {
  background-color: #4730e0;
  justify-content: center;
  padding: 10px 0;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  padding: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 11.25px);
}

.photo-card img {
  height: 200px;
  object-fit: cover;
}

.info {
  border: 1px solid #e7e9fc;
  justify-content: space-between;
  padding: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
}

.load-more {
  border-color: inherit;
  color: #fff;
  background-color: #4d5ae5;
  border-radius: 2px;
  margin: 10px auto 20px;
  padding: 7px 15px;
  display: none;
}

